.menu {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.menu-box {
  height: 100vh;
  width: 100vh;
  -webkit-transform: translate(100vw, -100vh);
      -ms-transform: translate(100vw, -100vh);
          transform: translate(100vw, -100vh);

  border-radius: 50%;
  background-color: #ccc;
  opacity: 0.5;
  -webkit-transition: .8s ease-out 0.3s;
  -o-transition: .8s ease-out 0.3s;
  transition: .8s ease-out 0.3s;
}

.menu.isOpen .menu-box {
  -webkit-transform: scale(4, 4);
      -ms-transform: scale(4, 4);
          transform: scale(4, 4);
  opacity: 1;
  -webkit-transition: .8s ease-in;
  -o-transition: .8s ease-in;
  transition: .8s ease-in;
}

.menu-items {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  position: absolute;
  top: 30%;
  left: 40%;
}

.menu.isOpen .menu-items {
  opacity: 1;
  visibility: visible;
  -webkit-transition: .3s ease-in 0.5s;
  -o-transition: .3s ease-in 0.5s;
  transition: .3s ease-in 0.5s;
}

a, a:hover, a:visited {
  color: #333;
  text-decoration: none;
  font-family: sans-serif;
  font-weight: 600;
}

a:hover {
  color: #eee;
}



