.nav-toggle {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  z-index: 3;
}

.shape {
  position: absolute;
  top: 20px;
  right: 30px;
  width: 46px;
  height: 46px;
  background-color: salmon;
  border-radius: 50%;
  -webkit-box-shadow: 0 3px 0 rgba(0,0,0,0.1);
          box-shadow: 0 3px 0 rgba(0,0,0,0.1);
  -webkit-transition: 0.2s linear 0.5s;
  -o-transition: 0.2s linear 0.5s;
  transition: 0.2s linear 0.5s;
}

.menu.isOpen .shape {
  background-color: #999;
}

.lines {
  position: absolute;
  top: 20px;
  right: 30px;
  width: 46px;
  height: 46px;
}

.lines.open {
  padding: 15px;
}

.lines.close {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.line  {
  position: absolute;
  background: #fff;
}

.lines.open .line {
  width: 16px;
  height: 2px;
  position: relative;
  margin-top: 3px;
  -webkit-transform: scaleX(0) translateX(0);
      -ms-transform: scaleX(0) translateX(0);
          transform: scaleX(0) translateX(0);
  -webkit-animation: open-in 0.3s 0.5s 1 normal forwards ease-out;
          animation: open-in 0.3s 0.5s 1 normal forwards ease-out;
}


.menu.isOpen .lines.open .line:nth-child(odd) {
  -webkit-animation: open-out-1 0.4s 0s 1 normal forwards linear;
          animation: open-out-1 0.4s 0s 1 normal forwards linear;
}

.menu.isOpen .lines.open .line:nth-child(even) {
  -webkit-animation: open-out-2 0.4s 0s 1 normal forwards linear;
          animation: open-out-2 0.4s 0s 1 normal forwards linear;
}


.lines.close .line:nth-child(1) {
  top: 15px;
  right: 22px;
  width: 2px;
  height: 16px;
  -webkit-transform: scaleY(0);
      -ms-transform: scaleY(0);
          transform: scaleY(0);
  -webkit-animation: close-out-1 0.3s 0.1s 1 normal forwards linear;
          animation: close-out-1 0.3s 0.1s 1 normal forwards linear;
}

.menu.isOpen .lines.close .line:nth-child(1) {
  -webkit-animation: close-in-1 0.3s 0.6s 1 normal forwards ease-in;
          animation: close-in-1 0.3s 0.6s 1 normal forwards ease-in;
}

.lines.close .line:nth-child(2) {
  position: absolute;
  top: 22px;
  right: 15px;
  width: 16px;
  height: 2px;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-animation: close-out-2 0.3s 0s 1 normal forwards linear;
          animation: close-out-2 0.3s 0s 1 normal forwards linear;
}

.menu.isOpen .lines.close .line:nth-child(2) {
  -webkit-animation: close-in-2 0.3s 0.5s 1 normal forwards ease-in;
          animation: close-in-2 0.3s 0.5s 1 normal forwards ease-in;
}

@-webkit-keyframes open-in {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
  }
}

@keyframes open-in {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
  }
}

@-webkit-keyframes open-out-1 {
  0% {
    -webkit-transform: scaleX(1) translate(0);
            transform: scaleX(1) translate(0);
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
  }
  35% {
    -webkit-transform: scaleX(0.9) translate(-5px);
            transform: scaleX(0.9) translate(-5px);
  }
  100% {
    -webkit-transform: scaleX(0) translate(5px);
            transform: scaleX(0) translate(5px);
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
  }
}

@keyframes open-out-1 {
  0% {
    -webkit-transform: scaleX(1) translate(0);
